footer{
    background-color: rgb(43, 43, 43);
    padding: 20px 0 40px 0;
    li{
        font-size: 23px;
        list-style-type: none;
        color: #FFF;
        margin-bottom: 15px;

        img{
            filter: invert(1);
        }

        i{
            margin-right: 20px;
        }
        a:link, a:visited{
            @include link;
            color: #FFF;
        }
    }
    li:nth-child(3) i{
        transform: rotate(-40deg);
    }

}